
import {computed, defineComponent, ref} from "vue";
import projectModule from "@/store/project";
import {Calendar} from "v-calendar";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'

export default defineComponent({
  name: "CalendarItem",
  components: {
    VueDatePicker
  },
  props: {
    data: {
      type: String,
      required: true,
    },
    year: {
      type: String,
      required: true,
    }
  },
  setup(props) {
    const date = ref(new Date());
    const months: any = {
      uk: ["", "", "", "", "", "", "", "", "", "", "", ""],
      ru: ["", "", "", "", "", "", "", "", "", "", "", ""],
    }

    const title = {
      uk: " -  , ,   .",
      ru: " -  , ,   ."
    }

    const currentLanguage = computed(() => projectModule.currentLanguage)
    const month = computed(() => months[currentLanguage.value][props.data])

    return {
      date,
      month,
      title,
      currentLanguage
    }
  }
});
